nav {
    width: 100vw;
    height: 5.5rem;
    display: grid;
    place-items: center;
    background: var(--color-primary);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    box-shadow: 0 1rem 1.5rem hsla(var(--primary-hue), 68%, 45%, 28%);
    transition: var(--transition);
}

.nav__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__logo {
    width: 6rem;
    overflow: hidden;
    top: 50%;
}

.nav__menu a{
    color: white;
}

.nav__menu {
    display: flex;
    grid-gap: 3.5em;
    gap: 3.5em;
}

#theme__icon {
    background: transparent;
    color: white;
    font-size: 2rem;
    cursor: pointer;
}

/* HIDE NAV MENU ON TABLETS AND PHONES */
@media screen and (max-width: 1024px) {
    .nav__menu {
        display: none;
    }
}

header {
    margin-top: 5rem;
    height: calc(100vh - 5rem);
    display: grid;
    place-items: center;
    background: var(--color-white);
    transition: var(--transition);
}

.header_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.header_profile {
    width: 18rem;
    height: 22rem;
    border-radius: 9rem 9rem 2rem 2rem;
    overflow: hidden;
    margin-bottom: 1.5rem;
}

.header_container p {
    width: 35rem;
    text-align: center;
    margin: 0.6rem 0 2rem;
}

.header_cta {
    display: flex;
    align-items: center;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.header_socials {
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.header_socials a {
    width: 1.8rem;
    aspect-ratio: 1/1;
    background: var(--color-light);
    color: var(--color-black);
    display: grid;
    place-items: center;
    border-radius: var(--border-radius-3);
}

.header_socials a:hover {
    background: var(--color-primary);
    color: var(--color-white);
    -webkit-transform: translateX(-0.5rem);
            transform: translateX(-0.5rem);
}


/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    header {
        margin-top: 0;
        height: 100vh;
    }
}


/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 600px) {
    .header_profile {
        width: 14rem;
        height: 17rem;
        max-width: 60%;
        max-height: 35vh;
    }

    .header_container p {
        width: 100%;
    }

    .header_cta {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .header_socials {
        display: none;
    }
}
.card{
    background: var(--color-white);
    padding: 1.5rem;
    border: 2px solid transparent;
    border-radius: var(--border-radius-1);
    transition: var(--transition);
}
.card:hover{
    background:transparent;
    border-color: var(--color-white) ;
    -webkit-transform:translateY(-0.5rem);
            transform:translateY(-0.5rem);
}
.card.light{
    background: var(--color-light);
}
.card.light:hover{
    background: transparent;
    border-color:var(--col-light);
}
@media screen and (max-width:600px){
    .card{
        padding:1.2rem;
    }
}
.about_container {
    display: grid;
    grid-template-columns: 35% 53%;
    grid-gap: 12%;
    gap: 12%;
}

.about_portrait {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-height: 40rem;
    border: 1rem solid var(--color-white);
    transition: var(--transition);    
}
.about_portrait img{
    max-height: 37rem;
}
.about_portrait:hover {
    border-width: 0;
}

.about_cards {
    margin: 3rem 0 1.5rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.card.about_card {
    text-align: center;
}

.about__card-icon {
    font-size: 1.3rem;
    color: var(--color-black);
    border-radius: var(--border-radius-3);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-inline: auto;
    padding: 0.6rem;
    display: flex;
    align-items: center;
    transition: var(--transition);
}

.card.about_card:hover .about_card-icon {
    color: var(--color-primary);
}

.card.about_card h5 {
    margin: 1rem 0;
}

.about_right p {
    margin-bottom: 0.8rem;
    text-align: center;
}

.about_right .btn {
    margin-top: 2.5rem;
}



/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    .about_left {
        display: none;
    }

    .about_container {
        grid-template-columns: 1fr;
    }

    .about_right h2 {
        text-align: center;
    }
}


/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {
.about_cards {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}

.about_right .btn {
    margin-top: 2rem;
}

}
.services__container{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-gap:2rem 4rem;
    gap:2rem 4rem;
}
.service__details{
    text-align: center;
}
.card.service{
    display: flex;
    align-items: flex-start;
    grid-gap:1rem;
    gap:1rem;
}
.service:hover .service__icon{
    background: var(--color-light);
    color:var(--color-primary);
}
.service__icon{
    background:var(--color-primary) ;
    padding:0.6rem;
    border-radius:var(--border-radius-3);
    font-size: 1.5rem;
    color:var(--color-white);
    transition:var(--transition);
}
.service h4{
    margin-bottom: .6rem;
}
#services h2{
    padding-bottom: 3rem;
}

@media screen and (max-width: 1024px){
    .services__container{
        grid-gap:1.5rem;
        gap:1.5rem;
    }
}

@media screen and (max-width: 600px){
    .services__container{
        grid-template-columns: 1fr;
        grid-gap:1.2rem;
        gap:1.2rem
    }
    .card.service{
        grid-gap:1.2rem;
        gap:1.2rem;
    }
}
.FAQS__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.5rem 4rem;
    gap: 1.5rem 4rem;
    align-items: start;
}

.card.faq:hover {
    cursor: pointer;
    background: var(--color-white);
}

.faq > div {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    grid-gap: 2rem;
    gap: 2rem;
}

.faq h5 {
    line-height: 1.4;
}

.faq__icon {
    font-size: 1.2rem;
    color: var(--color-black);
    background: transparent;
}

.faq__answer {
    margin-top: 1rem;
    opacity: 0;
    -webkit-animation: animateAnswer 500ms ease-in forwards;
            animation: animateAnswer 500ms ease-in forwards;
}

@-webkit-keyframes animateAnswer {
    to {
        opacity: 1;
    }
}

@keyframes animateAnswer {
    to {
        opacity: 1;
    }
}


/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    .faqs__container {
        grid-template-columns: 1fr;
    }
}


/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {
.faqs__container {
    grid-gap: 1rem;
    gap: 1rem;
}

}
